$headerHeight: 70px;
$darkerGrey: hsl(0, 0%, 72%);
$easeOut: cubic-bezier(.17,.67,.22,.94);
$appGrey: #E6E6E6;
$shopScrollbarWidth: 12px;
$appXpadding: 8vw;

@mixin fine-scrollbar {
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: transparent;
  }
  
  &::-webkit-scrollbar {
    width: 9px;
  }
  
  &::-webkit-scrollbar-thumb {
    z-index: 100;
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #555;
  }
}
@mixin fine-scrollbar-shop {
  &::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: transparent;
  }
  
  &::-webkit-scrollbar {
    width: 10px;
  }
  
  &::-webkit-scrollbar-thumb {
    z-index: 100;
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #555;
  }
}
@mixin fine-scrollbar-clean {
  &::-webkit-scrollbar-track {
    background-color: $appGrey;
  }
  
  &::-webkit-scrollbar {
    width: 9px;
  }
  
  &::-webkit-scrollbar-thumb {
    z-index: 100;
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #555;
  }
}

@media only screen and (max-width: 992px) {
  $appXpadding: 4vw;
}

@media only screen and (max-width: 768px) {
  $appXpadding: 2vw;
}

@media only screen and (max-width: 575px) {
  $appXpadding: 0vw;
}