.menu-item {
   min-width: 30%;
   height: 240px;
   flex: 1 1 auto;
   display: flex;
   align-items: center;
   justify-content: center;
   border: 1px solid black;
   margin: 0 7.5px 15px;
   overflow: hidden;
   position: relative;

   .background-image {
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
   }
   
   &:hover {
      cursor: pointer;
      .content {
         opacity: .9;
      }
      .background-image {
         transform: scale(1.1);
         transition: transform 6s cubic-bezier(.25, .45, .45, .95);
      }
   }

   .content {
     height: 90px;
     padding: 0 25px;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     border: 1px solid black;
     position: absolute;
     background-color: white;
     opacity: .7;
     transition: opacity 400ms;
     box-sizing: content-box;
 
     .title {
       font-weight: bold;
       margin-bottom: 6px;
       font-size: 22px;
       color: #4a4a4a;
       text-transform: uppercase;
     }
 
     .subtitle {
       font-weight: lighter;
       font-size: 16px;
       width: 90px;
       text-align: center;
     }
   }
 }

 @media only screen and (min-width: 689px) {
   .menu-item {
      &.large {
         height: 380px;
      }
   }
 }