$sub-color: grey;
$main-color: black;

@mixin shrinkLabel {
  bottom: 60px;
  font-size: 12px;
  color: $main-color;
}

.form-input {
  position: relative;
  margin: 45px 0;

  .input {
    background: none;
    background-color: white;
    color: $sub-color;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid $sub-color;
    margin: 20px 0;
    box-sizing: border-box;

    &:focus {
      outline: none;
    }

    &:focus ~ .input-label {
      @include shrinkLabel();
    }
  }

  input[type='password'] {
    letter-spacing: 0.3em;
  }

  .input-label {
    color: $sub-color;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    bottom: 30px;
    transition: 300ms ease all;

    &[shrink=true] {
      @include shrinkLabel();
    }
  }
}
